import {
    Vector3,
    CatmullRomCurve3,
    DoubleSide,
    TubeBufferGeometry,
    ShaderMaterial,
    Mesh
} from "three";
import {
    frequencyVertexShader,
    frequencyFragmentShader
} from './shaders'

export default class Infinitube {
    constructor(props) {
        const curve = new CatmullRomCurve3([
            new Vector3(0, 0, 0),
            new Vector3(0, 0, 750),
            new Vector3(0, 0, 1500),
            new Vector3(0, 0, 2250),
            new Vector3(0, 0, 3000),
        ]);

        const tubeMaterial = new ShaderMaterial({
            uniforms: props.UNIFORMS,
            vertexShader: frequencyVertexShader,
            fragmentShader: frequencyFragmentShader,
            transparent: true,
            defines: {
                PR: window.devicePixelRatio.toFixed(1),
            },
            side: DoubleSide
        });

        this.geometry = new TubeBufferGeometry(curve, 120, 3, 3, false);

        this.mesh = new Mesh(this.geometry, tubeMaterial);
    }
    
    setFrequencies(frequencyData) {
        this.geometry.setAttribute('frequency', frequencyData);
    }
}