import React from 'react';
import Visualizer from './3DStuff/visualizer';
import songUrl from "./music/disclosureEWM.mp3";
import Navbar, {NavButton} from './navigation/navbar';
import './App.css';
import './Animations/Animations.css';

class Iframe extends React.Component {
  render() {
    return (
      <iframe title="frame"
              src={this.props.src}
              frameBorder="0" 
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
              allowFullScreen>
     </iframe>
    )
  }
}

const lateJulyUrl = "https://www.youtube.com/embed/DR6TtBmJ-CE?autoplay=1&mute=1";
const miaUrl = "https://miamaddenmusic.com";

const home = <></>;
const page2 = <Iframe src={miaUrl}></Iframe>
const page3 = <Iframe src={lateJulyUrl}></Iframe>
const page4 = <div className="NavBody bgSawtooth"></div>;
const page5 = <Visualizer song={songUrl}></Visualizer>;

function App() {
  return (
    <div id="app" className="App">
      <Navbar>
        <NavButton body={home}> Home </NavButton>
        <NavButton body={page2}> M I A </NavButton>
        <NavButton body={page3}> Video </NavButton>
        <NavButton body={page4}> Trip </NavButton>
        <NavButton body={page5}> Ballz </NavButton>
      </Navbar>
    </div>
  );
}

export default App;
export {Iframe};