import { gsap, TimelineLite } from 'gsap/all';

function Animation(element) {
    const animation = new TimelineLite({onComplete: () => {
        animation.pause();
        animation.progress(0);
        element.style = "";
    }});
    return animation;
}

export function isAnimating(element) {
    return gsap.isTweening(element)
}

export function slideFromRight(props) {
    const {element, whenOffScreen} = props
    Animation(element)
        .to(element, 0.3, {x: -2000, opacity: 0})
        .to(element, 0, {x: 2000, onComplete: whenOffScreen})
        .to(element, 0.4, {x: 0, opacity: 1, ease: "elastic.out(.75, 1)"})
        .play();
}