import React from 'react';
import ReactDom from 'react-dom';
import { keyCodeMap } from '../util';
import * as Animations from "../Animations/Animations"
import './navbar.css';

class Navbar extends React.Component {
    bodyRef = React.createRef()
    state = {selectedIndex: 0}

    render() {
        this.children = React.Children.map(this.props.children, (child, index) => 
            !React.isValidElement(child) ? child 
            : React.cloneElement(child, {
                selected: this.state.selectedIndex === index,
                onClick: () => this.select(index)
            })
        )
        return (
            <>
            <div className="navbar">{this.children}</div>
            <div ref={this.bodyRef} id="navBody"></div>
            </>
        );
    }

    componentDidMount() {
        this.select(this.state.selectedIndex)
        window.addEventListener("keydown", this.onkeydown)
    }
    
    selectKey = key => typeof key === "number" && this.select(key - 1)

    select = (index) => {
        const wrappedIndex = index < 0 ? index + this.children.length : index % this.children.length
        if(this.state.selectedIndex === wrappedIndex) return 
        const body = this.bodyRef.current
        if(Animations.isAnimating(body)) return;
        const button = this.children[wrappedIndex]
        if(!button) return;

        this.setState({selectedIndex: wrappedIndex})

        Animations.slideFromRight({
            element: body, 
            whenOffScreen: () => ReactDom.render(button.props.body, body)
        })
    }

    onkeydown = (event) => {
        const key = keyCodeMap[event.keyCode]
        switch (key) {
            case "←":
                this.select(this.state.selectedIndex - 1)
                break;
            case "→":
                this.select(this.state.selectedIndex + 1)
                break;
            case "tab":
                event.preventDefault()
                this.select(this.state.selectedIndex + 1)
                break;
            default: this.selectKey(key)
        }
    }
}

class NavButton extends React.Component {
    render() {
        return (
            <span className={`NavButton ${this.props.selected ? 'selected' : ''}`} onClick={this.props.onClick}>
                {this.props.children}
            </span>
        )
    }
}

export {NavButton};
export default Navbar;