import {Audio, AudioLoader, AudioAnalyser, AudioListener} from "three";
import {keyCodeMap} from "../util"

class AudioPlayer {
    constructor(props) {
        this.props = props;
        this.props.frequencyCount = this.props.frequencyCount || 2048
        this.fftSize = this.props.frequencyCount * 2

        // This is how you create an html element in javascript
        this.button = document.createElement("div"); //<div></div>
        this.button.style = `
            position: fixed;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto;
            width: 135px;
            height: 135px;
            cursor: pointer;
            background: linear-gradient(210deg, transparent 150px, #e9afe3 150px) 42px -68px, 
                        linear-gradient(-30deg, transparent 150px, #e9afe3 150px) 42px 67px, 
                        linear-gradient(0deg, transparent 0%, #63b0ee 0%) 0px 0px;
            border-radius: 50%;
            box-shadow: 6px 3px 9px rgba(255, 255, 255, 0.3), 0 6px 6px rgba(255, 255, 255, 0.14);
        `;
        
        this.addEventListeners();
    }

    getFrequencyData = () => this.audioAnalyser.getFrequencyData();

    addEventListeners = () => {
        this.button.addEventListener("click", this.toggleAudioPlay);
        window.addEventListener("keyup", this.onKeyUp);
        if(this.props.audioFinishedPlaying) this.audio.addEventListener('ended', this.props.audioFinishedPlaying);
    }

    removeEventListeners = () => {
        this.button.removeEventListener("click", this.handleClick);
        window.removeEventListener("keyup", this.onKeyUp);
        if(this.props.audioFinishedPlaying) this.audio.removeEventListener('ended', this.props.audioFinishedPlaying);
    }

    dispose = () => {
        this.removeEventListeners()
        if(this.audio) this.audio.pause()
    }

    animateButton = () => this.button.classList.add("spin-animation")

    stopButtonAnimation = () => this.button.classList.remove("spin-animation")

    loadAudio = () => {
        this.isLoading = true;
        this.button.parent = this.button.parentNode
        this.animateButton();
        const listener = new AudioListener();
        this.audio = new Audio(listener);
        this.props.camera.add(listener);
    
        const loader = new AudioLoader();
        loader.load(this.props.song, function(buffer) {
            this.audio.setBuffer(buffer)
            this.stopButtonAnimation();
            this.isLoading = false;
            this.toggleAudioPlay();
        }.bind(this));

        this.audioAnalyser = new AudioAnalyser(this.audio, this.fftSize);
    }

    onKeyUp = (event) => {
        if(keyCodeMap[event.keyCode] === "space") this.toggleAudioPlay()
    }

    toggleAudioPlay = () => {
        if(this.isLoading) return;

        if(!this.button.parent) {
            this.loadAudio()
        } else if(this.audio.isPlaying) {
            this.button.parent.appendChild(this.button)
            this.audio.pause()
            if(this.props.onPause) this.props.onPause()
        } else {
            this.button.parent.removeChild(this.button)
            this.audio.play()
            if(this.props.onPlay) this.props.onPlay()
        }
    }
}

export default AudioPlayer;